.gameInside {
    height: 100%;
    .container {
        width: 800px;
        @media (max-width: 1366px) {
            & {
                width: 700px;
            }
        }
        header {
            width: 100%;
            height: 15vh;
            display: flex;
            justify-content: space-between;
            align-items: center;
            img {
                height: 70px;
                @media (max-width: 1366px) {
                    & {
                        height: 55px;
                    }
                }
            }
            button {
                @extend .smoothPointer;
                width: 115px;
                padding: .65rem;
                border-radius: 20px;
                background-color: $Button_BG;
                color: #fff;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-weight: 700;
                &:hover {
                    filter: brightness(.9);
                }
                &:active {
                    transform: scale(.93);
                }
                @media (max-width: 1366px) {
                    & {
                        width: 100px;
                        font-size: .75rem;
                    }
                }
            }
        }
        .content {
            position: relative;
            margin-top: 2rem;
            .playersPanel {
                display: flex;
                justify-content: space-between;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 150%;
                @media (max-width: 1366px) {
                    & {
                        width: 135%;
                    }
                }
                .firstPlayer,
                .secondPlayer {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    background-color: #fff;
                    padding: 2.85rem 1.75rem 1.65rem;
                    border-radius: 20px;
                    border: 3px solid #000;
                    box-shadow: 0 9px 0 #000;
                    position: relative;
                    @media (max-width: 1366px) {
                        & {
                            transform: scale(.85);
                        }
                    }
                    .icon {
                        @extend .center-g;
                        height: 50px;
                        width: 50px;
                        border: 3px solid #000;
                        box-shadow: 0 5px 0 #000;
                        border-radius: 50%;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        top: -25px;
                        .happyFace {
                            transform: scale(1.2);
                        }
                    }
                    .playerNum {
                        font-size: 1.25rem;
                        font-weight: 600;
                        text-transform: uppercase;
                        margin-bottom: 0.45rem;
                    }
                    > span {
                        font-size: 3rem;
                        font-weight: 500;
                        transform: scale(1.2);
                    }
                }
                .firstPlayer {
                    .icon {
                        background-color: $red;
                    }
                }
                .secondPlayer {
                    .icon {
                        background-color: $yellow;
                        .happyFace {
                            transform: scale(1.2) rotateY(180deg);
                        }
                    }
                }
            }
            .gamePanel {
                @extend .center-g;
                .panel_Wrapper {
                    width: 86%;
                    background-color: #fff;
                    border: 3px solid #000;
                    border-radius: 25px;
                    box-shadow: 0 9px 0 #000;
                    padding: 0.625rem 0.625rem 2.625rem;
                    z-index: 9;
                    @media (max-width: 1366px) {
                        & {
                            width: 75%;
                        }
                    }
                    .disks_Container {
                        display: flex;
                        flex-wrap: wrap;
                        div {
                            @extend .smoothPointer;
                            height: 70px;
                            width: 70px;
                            border: 3px solid #000;
                            border-radius: 50%;
                            margin: .65rem;
                            background-color: $Background_Color;
                            box-shadow: inset 0 8px 0 #000;
                            &:active {
                                transform: scale(.95);
                            }
                            @media (max-width: 1366px) {
                                & {
                                    height: 55px;
                                    width: 55px;
                                    margin: 0.4rem;
                                }
                            }
                        }
                    }
                }
            }
            .counter {
                @extend .center-g;
                position: absolute;
                left: 50%;
                bottom: -40px;
                transform: translateX(-50%);
                z-index: 99;
                @media (max-width: 1366px) {
                    & {
                        bottom: -20px;
                        transform: translateX(-50%) scale(.85);
                    }
                }
                svg {
                    height: 150px;
                    position: absolute;
                }
                .info_wrapper {
                    position: relative;
                    @extend .center-g;
                    .info {
                        position: absolute;
                        text-align: center;
                        transform: translateY(0.625rem);
                        color: #fff;
                        .current_player {
                            text-wrap: nowrap;
                            font-weight: 500;
                            text-transform: uppercase;
                            margin-bottom: 0.375rem;
                        }
                        .time {
                            font-size: 3rem;
                            font-weight: 600;
                            text-transform: lowercase;
                        }
                    }
                }
            }
        }
    }
}